.recharge .personal-title {
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 32px;
  color: #000;
  text-align: center;
  margin-top: 32px;
}
.recharge .recharge-body {
  width: 853px;
  height: 652px;
  border-radius: 8px;
  border: 1px solid rgba(0, 0, 0, 0.06);
}
.recharge .recharge-center {
  width: 652px;
  height: 515px;
  background: #f8f8f8;
  margin: 0 auto;
  padding: 40px;
}
.recharge .recharge-center p {
  margin-bottom: 0;
}
.recharge .recharge-center .center-title {
  text-align: center;
  font-size: 12px;
  font-weight: 400;
  color: rgba(0, 0, 0, 0.5);
  line-height: 16px;
  height: 16px;
  margin-bottom: 30px;
}
.recharge .recharge-center .center-address {
  font-weight: 600;
  text-align: center;
}
.recharge .recharge-center .recharge-btn {
  width: 130px;
  height: 32px;
  background: #0091ff;
  margin: 20px auto;
  border-radius: 4px;
  text-align: center;
  line-height: 32px;
  color: #fff;
}
.recharge .recharge-center .qrcode {
  width: 120px;
  margin: 30px auto;
}
.recharge .recharge-center .recharge-footer {
  margin-left: 60px;
  text-align: left;
  font-size: 10px;
}
@media only screen and (max-width: 768px) {
  .recharge {
    width: 100%;
  }
  .recharge .personal-title {
    margin: 15px 0;
  }
  .recharge .recharge-body {
    width: 100%;
  }
  .recharge .recharge-center {
    width: 100%;
    height: 560px;
    padding: 40px 10px 10px 10px;
  }
  .recharge .recharge-center .qrcode {
    width: 120px;
    margin: 30px auto;
    margin-bottom: 10px;
  }
  .recharge .recharge-center .recharge-footer {
    margin-left: 10px;
  }
}
